import React from 'react';

class GithubIcon extends React.Component {
  render() {
    return(
      <svg width="75" height="75" viewBox="0 0 75 75" fill="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
      <mask id="githubmask" mask-type="alpha" maskUnits="userSpaceOnUse" x="7" y="3" width="62" height="62">
      <rect x="7" y="7" width="62" height="62" fill="url(#githubpattern)"/>
      </mask>
      <g mask="url(#githubmask)">
      <rect width="75" height="75" fill="url(#github_paint)"/>
      </g>
      <defs>
      <pattern id="githubpattern" patternContentUnits="objectBoundingBox" width="1" height="1">
      <use xlinkHref="#github" transform="scale(0.0011236)"/>
      </pattern>
      <linearGradient id="github_paint" x1="0" y1="37.4999" x2="75" y2="37.4999" gradientUnits="userSpaceOnUse">
      <stop offset="0.290702" stopColor={this.props.color2}/>
      <stop offset="0.768335" stopColor={this.props.color1}/>
      </linearGradient>
      <image id="github" width="890" height="890" xlinkHref="data:image/png;base64,"/>
      </defs>
      </svg>
    );
  }
}

export default GithubIcon
