import React from 'react';

class LinkedInIcon extends React.Component {
  render() {
    return(
      <svg width="75" height="75" viewBox="0 0 75 75" fill="none" xmlns="http://www.w3.org/2000/svg">
      <mask id="LImask" mask-type="alpha" maskUnits="userSpaceOnUse" x="0" y="0" width="75" height="75">
      <rect width="75" height="75" fill="url(#LIpattern)"/>
      </mask>
      <g mask="url(#LImask)">
      <rect width="75" height="75" fill="url(#paint0_linear)"/>
      <rect x="-19" width="75" height="75" fill="url(#LI_paint)"/>
      </g>
      <defs>
      <pattern id="LIpattern" patternContentUnits="objectBoundingBox" width="1" height="1">
      <use href="#linkedin" transform="scale(0.00196078)"/>
      </pattern>
      <linearGradient id="paint0_linear" x1="0" y1="37.4999" x2="75" y2="37.4999" gradientUnits="userSpaceOnUse">
      <stop offset="0.0246423" stopColor={this.props.color2}/>
      <stop offset="1" stopColor={this.props.color1}/>
      </linearGradient>
      <linearGradient id="LI_paint" x1="-19" y1="37.4999" x2="56" y2="37.4999" gradientUnits="userSpaceOnUse">
      <stop offset="0.0246423" stopColor={this.props.color2}/>
      <stop offset="1" stopColor={this.props.color1}/>
      </linearGradient>
      <image id="linkedin" width="510" height="510" href="data:image/png;base64,"/>
      </defs>
      </svg>
    );
  }
}

export default LinkedInIcon
