import React from 'react';

class Logo extends React.Component {
  render() {
    return(
      <svg width="75" height="54" viewBox="0 0 75 54" fill="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
      <mask id="logomask" mask-type="alpha" maskUnits="userSpaceOnUse" x="0" y="0" width="75" height="54">
      <rect width="75" height="53.8773" fill="url(#logopattern)"/>
      </mask>
      <g mask="url(#logomask)">
      <rect width="75" height="54" fill="url(#logo_paint)"/>
      </g>
      <defs>
      <pattern id="logopattern" patternContentUnits="objectBoundingBox" width="1" height="1">
      <use xlinkHref="#logo" transform="scale(0.000385802 0.000537057)"/>
      </pattern>
      <linearGradient id="logo_paint" x1="0" y1="26.9999" x2="75" y2="26.9999" gradientUnits="userSpaceOnUse">
      <stop offset="0.0246423" stopColor={this.props.color2}/>
      <stop offset="1" stopColor={this.props.color1}/>
      </linearGradient>
      <image id="logo" width="2592" height="1862" xlinkHref="data:image/png;base64,"/>
      </defs>
      </svg>

    )
  }
}

export default Logo
